



































































































$color-primary:rgb(41, 21, 114);

.search-wrapper {
  width: 300px;
  margin: 0 auto;
  input {
    font-size: 20px;
    width: 100%;
    height: 20px;
    border: 1px solid $color-primary;
    padding: 5px;
    border-radius: 5px;
    margin-left: -5px;
  }
}
.inhabitant__card {
  width: 90%;
  border: 1px solid #555;
  border-radius: 5px;
  padding: 10px;
  margin: 10px auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.5);
  transition: height 0.3s, box-shadow 0.3s;
  &:hover {
    box-shadow: 20px 20px 40px 0px rgba(0, 0, 0, 0.5);
  }
  .inhabitant__info {
    margin: 0 auto;
    text-align: center;
    flex-grow: 2;
  }

  .inhabitant__image {
    max-width: 150px;
    height: 150px;
    margin: 0 auto;
    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }
  }

  .detail__button {
    padding: 20px;
    text-align: center;
  }
}

@media (min-width: 768px) {
  .inhabitant__card {
    flex-direction: row;
    justify-content: center;
  }
}
